export default {
	data() {
		return {
			card1Show:false,
			card2Show:false,
			card3Show:false,
			item:['单卡提交','批量提交'],
			current:0,
			src:'',
			card1List:[],//卡类列表数组
			card2List:[],//卡种列表数组
			card3List:[],//面值列表数组
			token:'',//onShow时获取token存起来，以便每次发送请求都要重新获取
			card1Name:'',//选择的卡类名称
			card1Logo:'',//选择的卡类logo
			card1Id:'',//选择的卡类id
			card2Name:'',//选择的卡种名称
			card2Logo:'',//选择的卡种logo
			card2Id:'',//选择的卡种id
			hasCardNumber:'0',//0为有卡号 1为无卡号
			card3Id:'',//选择的面值id
			card3FaceValue:'',//选择的面值
			card3Discount:'',//选择的面值折扣
			discountPrice:'',//回收价
			remark:'',//备注
			cardNumber:'',//单卡提交输入的卡号
			cardPsd:'',//单卡提交输入的卡密
			cards:'',//批量提交
			name:'',
			phone:'',
			commitMemuShow:false,//提交蒙版显示隐藏
		}
	},
	onShow(){
		this.src = this.url
		this.token = uni.getStorageSync('token')
		let card1Id = uni.getStorageSync('card1Id')
		let card2Id = uni.getStorageSync('card2Id')
		let _this = this
		uni.request({
			url: this.url + '/mobile/classificationData',
			method:'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				type:2
			},
			success: (res) => {
				console.log(res);
				_this.card1List = res.data.data.classList
				if(card1Id){
					this.card1Id = card1Id
					for(let i = 0 ; i < _this.card1List.length ; i++){
						if(_this.card1List[i].id == card1Id){
							_this.card1Id = _this.card1List[i].id
							_this.card1Name = _this.card1List[i].name
							_this.card1Logo = _this.card1List[i].logo
						}
					}
				}
			}
		})
		if(card1Id){
			uni.removeStorageSync('card1Id')
			uni.request({//获取卡种列表
				url: _this.url + '/mobile/cardListData',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': _this.token
				},
				data:{
					type:2,
					commodityClassId:card1Id
				},
				success: (res) => {
					console.log(res);
					_this.card2List = res.data.data
					if(card2Id){
						console.log(card2Id)
						this.card2Id = card2Id
						for(let j = 0 ; j < _this.card2List.length ; j++){
							if(_this.card2List[j].id == card2Id){
								_this.card2Id = _this.card2List[j].id
								_this.card2Name = _this.card2List[j].name
								_this.card2Logo = _this.card2List[j].imgPath
							}
						}
					}
				}
			})
		}
		if(card2Id){
			uni.removeStorageSync('card2Id')
			uni.request({//获取面值列表
				url: _this.url + '/mobile/getFaceVlaueData',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': _this.token
				},
				data:{
					type:2,
					commodityId:card2Id
				},
				success: (res) => {
					console.log(res);
					_this.card3List = res.data.data
					for(let i=0;i<_this.card3List.length;i++){
						_this.card3List[i].discountPrice = (_this.card3List[i].faceValue*_this.card3List[i].discount).toFixed(2)
						_this.card3List[i].discount = (_this.card3List[i].discount*10).toFixed(1)
					}
				}
			})
		}
		uni.request({
			url: this.url + '/mobile/realData',
			method:'GET',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				
			},
			success: (res) => {
				console.log(res);
				_this.name = res.data.realName
				_this.phone = res.data.alipayAccount
			}
		})
	},
	methods: {
		commitCard(){//蒙版点击确认提交
			if(this.current == '0'){//单卡提交
				// 发送请求
				uni.request({
					url: this.url + '/mobile/saveCard',
					method:'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' ,// 默认值
						token:this.token
					},
					data:{
						commodityId: this.card2Id ,//卡种id
						feedbackId: this.card3Id,//面值id
						cardNumber: this.cardNumber,
						cardPassword: this.cardPsd,
						alipayAccount: this.phone,
						realName: this.name,
						type: 0,//0为单卡提交，1为多卡提交
						hasCardNumber: this.hasCardNumber,
					},
					success: (res) => {
						console.log(res);
						this.commitMemuShow = false
						if(res.data.result){
							uni.showToast({
								title: '提交成功',
								duration: 2000,
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								icon:'none',
								duration: 2000,
							});
						}
					},
					error:function(){
						uni.showToast({
							title: '网络错误,请稍后再试',
							icon:'none',
							duration: 2000,
						});
					}
				})
			}else{//批量提交
				// 发送请求
				uni.request({
					url: this.url + '/mobile/saveCard',
					method:'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' ,// 默认值
						token:this.token
					},
					data:{
						commodityId: this.card2Id ,//卡种id
						feedbackId: this.card3Id,//面值id
						alipayAccount: this.phone,
						realName: this.name,
						cards:this.cards,//批量提交字符串
						type: 1,//0为单卡提交，1为多卡提交
						hasCardNumber: this.hasCardNumber,
					},
					success: (res) => {
						console.log(res);
						this.commitMemuShow = false
						if(res.data.result){
							uni.showToast({
								title: '提交成功',
								duration: 2000,
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								icon:'none',
								duration: 2000,
							});
						}
					},
					error:function(){
						uni.showToast({
							title: '网络错误,请稍后再试',
							icon:'none',
							duration: 2000,
						});
					}
				})
			}
		},
		closeMemu(){
			this.commitMemuShow = false
		},
		commit(){//提交事件
			let _this = this
			if(this.name=='' || this.phone==''){
				uni.showModal({
					title: '提交',
					content:'请先绑定打款账户',
					success:function(){
						uni.navigateTo({
							url: '../account/account'
						})
					}
				})
				return false
			}
			
			if(this.current == '0'){//单卡提交
				if(this.hasCardNumber == '0'){//有卡号
					if(this.cardNumber == ''){
						uni.showModal({
							title: '提交',
							content:'请输入礼品卡卡号'
						})
						return false
					}else if(this.cardPsd == ''){
						uni.showModal({
							title: '提交',
							content:'请输入礼品卡卡密'
						})
						return false
					}
					this.commitMemuShow = true
				}else{//无卡号
					if(this.cardPsd == ''){
						uni.showModal({
							title: '提交',
							content:'请输入礼品卡卡密'
						})
						return false
					}
					this.commitMemuShow = true
				}
			}else{//批量提交
				if(this.cards == ''){
					uni.showModal({
						title: '提交',
						content:'请至少输入一张礼品卡信息'
					})
					return false
				}
				this.commitMemuShow = true
			}
		},
		scanningCard(){//扫描卡号二维码逻辑
			let _this = this
			console.log(12345678978)
			uni.scanCode({
				onlyFromCamera:false,
				success:function(res){
					console.log(res.result)//扫描二维码出来的数据
					_this.cardNumber = res.result
				}
			})
		},
		scanningPsd(){//扫描卡密二维码逻辑
			let _this = this
			uni.scanCode({
				onlyFromCamera:false,
				success:function(res){
					console.log(res.result)//扫描二维码出来的数据
					_this.cardPsd = res.result
				}
			})
		},
		
		card1Click(){//卡类点击出现选择框
			this.card1Show = true
		},
		card1Close(){
			this.card1Show = false
		},
		pitchCard1(id,name,logo){
			// 清空卡种和面值的值
			this.card2Id = ''
			this.card2Name = ''
			this.card2Logo = ''
			this.hasCardNumber = ''
			this.card3Id = ''
			this.card3FaceValue = ''
			this.card3Discount = ''
			this.discountPrice = ''
			let _this = this
			this.card1Id = id
			this.card1Name = name
			this.card1Logo = logo
			this.card1Show = false
			uni.request({//获取卡种列表
				url: _this.url + '/mobile/cardListData',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': _this.token
				},
				data:{
					type:2,
					commodityClassId:_this.card1Id
				},
				success: (res) => {
					console.log(res);
					_this.card2List = res.data.data
				}
			})
		},
		card2Click(){//卡种点击出现选择框
			this.card2Show = true
		},
		card2Close(){
			this.card2Show = false
		},
		pitchCard2(id,name,src,hasCardNumber,remark){//卡种选择事件
			this.card3Id = ''
			this.card3FaceValue = ''
			this.card3Discount = ''
			this.discountPrice = ''
			let _this = this
			this.card2Id = id
			this.card2Name = name
			this.card2Logo = src
			this.remark = remark
			this.hasCardNumber = hasCardNumber
			this.card2Show = false
			uni.request({//获取面值列表
				url: _this.url + '/mobile/getFaceVlaueData',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': _this.token
				},
				data:{
					type:2,
					commodityId:_this.card2Id
				},
				success: (res) => {
					console.log(res);
					_this.card3List = res.data.data
					for(let i=0;i<_this.card3List.length;i++){
						_this.card3List[i].discountPrice = (_this.card3List[i].faceValue*_this.card3List[i].discount).toFixed(2)
						_this.card3List[i].discount = (_this.card3List[i].discount*10).toFixed(1)
					}
				}
			})
		},
		card3Click(){//面值点击出现选择框
			this.card3Show = true
		},
		card3Close(){
			this.card3Show = false
		},
		pitchCard3(id,faceValue,discount,discountPrice){
			this.card3Id = id
			this.card3FaceValue = faceValue
			this.card3Discount = (discount*10).toFixed(1)+'折'
			this.discountPrice = discountPrice
			this.card3Show = false
		},
		changeTab(index){
			this.current = index
		}
	}
}
